<template>
<div id="QRCODE">
  <img v-bind:src="QrUrl" alt="二维码图片" style="width:100%">
  <!-- 打赏按钮 -->
    <button class="btn btn-warning btn-block navbar-fixed-bottom" style="font-size:20px;background-color:rgb(44,137,58)" v-on:click="giveMoney">
            打赏
    </button>
         <!-- 打赏模态框 -->
    <div class="modal fade bs-example-modal-sm" role="dialog" id="giveMoney">
            <div class="modal-dialog modal-sm" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        请输入打赏金额
                    </div>
                      <div class="modal-body" style="width:100%">
                          <button class="btn btn-default  moneyBtn" style="width:40%" v-for="item in moneyList" v-on:click="weChatPay(item)">{{item}}元</button>
                          <input type="number" placeholder="请输入金额" class="form-control" v-model="money">
                      </div> 
                    <div class="modal-footer" style="height:60px">
                        <button type="button" class="btn btn-primary" v-on:click="weChatPay(money)">确定</button>
                        <button type="button" class="btn btn-danger" data-dismiss="modal">关闭</button>
                    </div> 
                </div>
            </div>
        </div>
        </div>
</template>

<script>
import global from "./global";

export default {
  name:"QRCODE",
  data() {
    return {
       sendTemp: {
        htlcode: "",
        posno: "",
        opno: "",
        star: "",
        comment: " ",
        money: "",
        unique: "",
        action: "",
        typeno: "",
        openId: "",
        telno: "",
        total_fee: ""
      },
      GetQrCode: {
        htlcode: "",
        posno: "",
        opno: "",
        action: "GetQrCode",
        openid: ""
      },
        WxPayData: {
        appId: "",
        package: "",
        nonceStr: "",
        paySign: "",
        signType: "",
        timeStamp: ""
      },
      QrUrl: "",
      money: null,
      moneyList: [1, 5, 10, 20]
    };
  },
  created: function() {
    this.GetQrCode.opno = global.opno;
    this.GetQrCode.htlcode = global.htlcode;
    this.GetQrCode.posno = global.posno;
    this.GetQrCode.openid = global.OpenId;
    // alert(this.GetQrCode.openid);
    this.$http
      .post(global.PostUrl, JSON.stringify(this.GetQrCode))
      .then(Response => {
        this.QrUrl = Response.body;
      });
  },
  methods: {
    giveMoney: function() {
      $("#giveMoney").modal("show");
    },
    // 打赏的方法
    weChatPay: function(payMoney) {
      $("#giveMoney").modal("hide");
      this.sendTemp.action = "WxPayApi";
      this.sendTemp.htlcode = global.OpnoInfo.htlcode;
      this.money = payMoney;
      this.sendTemp.openId = global.OpenId;
      this.sendTemp.total_fee = payMoney;
      this.$http
        .post(global.PostUrl, JSON.stringify(this.sendTemp))
        .then(Response => {
          //
          this.WxPayData.appId = Response.body.appId;
          this.WxPayData.nonceStr = Response.body.nonceStr;
          this.WxPayData.package = Response.body.package;
          this.WxPayData.signType = Response.body.signType;
          this.WxPayData.timeStamp = Response.body.timeStamp;
          this.WxPayData.paySign = Response.body.paySign;
          //
          this.sendTemp.htlcode = global.OpnoInfo.htlcode;
          this.sendTemp.posno = global.OpnoInfo.posno;
          this.sendTemp.opno = global.OpnoInfo.opno;
          this.sendTemp.unique = global.userRandom;
          this.sendTemp.telno = global.telno;
          this.sendTemp.typeno = "money";
          this.sendTemp.action = "payMoney";
          this.onBridgeReady(this.sendTemp);
        });
      if (this.money == null || this.money == "" || this.money == " ") {
        alert("您输入的金额有误，请重新输入");
        return;
      }
    },
    PostPayData: function() {
      alert("InPostPayData");
      this.sendTemp.htlcode = global.OpnoInfo.htlcode;
      this.sendTemp.posno = global.OpnoInfo.posno;
      this.sendTemp.opno = global.OpnoInfo.opno;
      this.sendTemp.unique = global.userRandom;
      this.sendTemp.typeno = "Pay";
      this.sendTemp.action = "payMoney";
      this.$http.post(global.PostUrl, this.sendTemp).then(Response => {
        alert(Response.body);
        if (Response.body == "Success") {
          history.go(0);
        } else {
        }
      });
    },
    onBridgeReady: function(dataTemp) {
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: this.WxPayData.appId, //公众号名称，由商户传入
          timeStamp: this.WxPayData.timeStamp, //时间戳，自1970年以来的秒数
          nonceStr: this.WxPayData.nonceStr, //随机串
          package: this.WxPayData.package,
          signType: "MD5", //微信签名方式：
          paySign: this.WxPayData.paySign //"70EA570631E4BB79628FBCA90534C63FF7FADD89" //微信签名
        },
        function(res) {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            $.ajax({
              type: "POST",
              url: global.PostUrl,
              data: JSON.stringify(dataTemp),
              contentType: "application/json"
            });
          } // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
        }
      );
    }
  }
};
</script>

<style>

</style>



