<template>
  <div id="AppCom">
    <div style="width:100%;margin-bottom:50px;">
      <img src="../assets/logo.png" style="width:90%;">
    </div>
    <infoDisplay style="margin-bottom:20px"></infoDisplay>
    <comand></comand>
  </div>
</template>

<script>
import infoDisplay from "./infoDisplay";
import comand from "./comand";
import global from "./global";

export default {
  name: "AppCom", 
  components: {
    infoDisplay,
    comand
  },
  data() {
    return {
      qrInfo: {
        opno: "",
        htlcode: "", 
        posno: "",
        action: "GetOpnoInfo"
      }
    };
  },
  created: function() {
    global.userRandom = Date.now() + Math.random();
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>  


