<template>
    <div id="infoDisplay" class="divMagrin">
        <div class="media divMargin" style="margin:30px 30px">
            <div class="media-left media-middle">
                <img class="media-object" v-bind:src="imageUrl" alt="头像信息" height="100px" width="80px">
            </div>
            <div class="media-body" style="font-size:medium">
                <span style="color:gray;font-weight:normal">姓名:</span>
                <span v-text="opName"></span>
                <br>
                <br>
                <span style="color:gray;font-weight:normal">工号:</span>
                <span v-text="opno"></span>
            </div>
        </div>
    </div>
</template>

<script>
import global from "./global";

export default {
  name: "infoDisplay",
  data() {
    return {
      opno: "",
      opName: "",
      imageUrl: "",
      htlcode: "",
      OpenId: "",
      qrInfo: {
        opno: "",
        htlcode: "",
        posno: "",
        action: "GetOpnoInfo"
      },
      checkIsUser: {
        action: "CheckUser",
        openId: "",
        htlcode:""
      }
    };
  },
  created: function() {
    this.UrlSearch();
    this.$http
      .post(global.PostUrl, JSON.stringify(this.checkIsUser))
      .then(Response => {
        if(Response.body=="NoUser"){
          location.href="#/QrCode";
        }
        else{
          global.telno = Response.body;
        }
        
      });
    this.$http
      .post(
        global.PostUrl,
        JSON.stringify(this.qrInfo)
      )
      .then(Response => {
        global.OpnoInfo = Response.body;
        this.opno = global.OpnoInfo.opno;
        this.opName = global.OpnoInfo.name;
        this.imageUrl = global.OpnoInfo.image;
      });
  },
  methods: {
    //获取URL参数
    UrlSearch: function() {
      var name, value;
      var str = location.href; //取得整个地址栏
     // alert(str);
      str = decodeURI(str);
      var num = str.indexOf("?");
      str = str.substr(num + 1); //取得所有参数 stringvar.substr(start [, length ]
      var arr = str.split("&"); //各个参数放到数组里
      for (var i = 0; i < arr.length; i++) {
        num = arr[i].indexOf("=");
        if (num > 0) {
          name = arr[i].substring(0, num);
          value = arr[i].substr(num + 1);
          this[name] = value;
          //s;
        }
      }
      this.qrInfo.posno = this.posno.replace("#/", "");
      this.qrInfo.opno = this.opno.replace("#/", "");
      this.qrInfo.htlcode = this.htlcode.replace("#/", "");
      global.OpenId = this.openid.replace("#/", "");
      global.opno =this.opno.replace("#/", "");
      global.htlcode= this.qrInfo.htlcode;
      global.posno=this.qrInfo.posno ;
      this.checkIsUser.openId = this.openid.replace("#/", "");
      this.checkIsUser.htlcode=global.htlcode;
      //alert(global.OpenId);
    }
  }
};
</script>

<style scope>
.divMagrin {
  margin-left: 5%;
  margin-right: 5%;
}

.btn-Money {
  height: 80px;
  font-weight: bold;
}
</style>



