import Vue from 'vue'
import Router from 'vue-router'
import infoDisplay from '@/components/infoDisplay'
import LoadOpenId from '@/components/LoadOpenId'
import AppCom from '@/components/AppCom'
import QrCode from '@/components/QrCode'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/AppCom',
      name: 'AppCom',
      component: AppCom
    },
    {
      path:'/LoadOpenId',
      name:'LoadOpenId',
      component:LoadOpenId
    },
    {
      path:'/QrCode',
      name:'QrCode',
      component:QrCode
    }
  ]
})



