<template>
  <div id="LoadOpenId">
      <h1>正在加载，请稍候</h1>
  </div>
</template>

<script>
import global from "./global";
  
export default {
  name: "LoadOpenId",/*
  
  */ 
  data() {
    return {
      opno: "",
      posno: "",
      htlcode: "",
      OpenId: "",
      UrlString: "http://www.wedawechat.com/api/oauth_login.aspx?wid=69&rf=",//"http://www.wedawechat.com/api/oauth_login.aspx?wid=48&rf=",
      postTemp: {
        action: "Urlencode",
        url:
       "http://m.betrue-cy.com:8082/#/AppCom?opno={opno}&htlcode={htlcode}&posno={posno}&openid={openid}"//&telno={telno}
         // "http://comment.wedawechat.com/#/AppCom?opno={opno}&htlcode={htlcode}&posno={posno}&openid={openid}"
      }
    };
  },
  created: function() {
    this.UrlSearch();
    this.postTemp.url = this.postTemp.url.replace("{opno}", this.opno);
    this.postTemp.url = this.postTemp.url.replace("{htlcode}", this.htlcode);
    this.postTemp.url = this.postTemp.url.replace("{posno}", this.posno);
    console.log('post');
    this.$http.post(global.PostUrl, this.postTemp).then(Response => {
      console.log(Response);
      this.UrlString += Response.body;
      console.log(this.UrlString);
      location.href = this.UrlString;
    });
   },
  methods: {
    UrlSearch: function() {
      var name, value;
      var str = location.href; //取得整个地址栏
      var num = str.indexOf("?");
      str = str.substr(num + 1); //取得所有参数   stringvar.substr(start [, length ]
      var arr = str.split("&"); //各个参数放到数组里
      for (var i = 0; i < arr.length; i++) {
        num = arr[i].indexOf("=");
        if (num > 0) {
          name = arr[i].substring(0, num);
          value = arr[i].substr(num + 1);
          this[name] = value;
        }
      }
      global.OpnoInfo.posno = this.posno = this.posno.replace("#/", "");
      global.OpnoInfo.opno = this.opno = this.opno.replace("#/", "");
      global.OpnoInfo.htlcode = this.htlcode = this.htlcode.replace("#/", "");
    }
  }
};
</script>

<style>

</style>



