<template>
    <div id="comand" class="divMagrin">
        <label>评星</label>
        <div class="rating rating2" style="font-size:30px">
            <span v-for="item in stars" style="margin-left: 5px;" v-bind:class="{yellowActive:item.active}" v-on:click="clickStar(item.num)">★</span>
        </div>

        <!-- 建议框 -->
        <textarea class="form-control" rows="3" v-model="comandText" placeholder="请输入评价内容"></textarea>
        <button class="btn btn-primary btn-block" style="margin-top:10px;background-color:rgb(44,137,58)" v-on:click="submit">提交</button>
        <div style="height:50px"></div>
        <!-- 打赏框 -->
        <button class="btn btn-warning btn-block navbar-fixed-bottom" style="font-size:20px;background-color:rgb(44,137,58)" v-on:click="giveMoney">
            打赏
        </button>
        <!-- 模态框 -->

        <!-- 提交评论 -->
        <div class="modal fade bs-example-modal-sm" role="dialog" id="myModal">
            <div class="modal-dialog modal-sm" role="document">
                <div class="modal-content">
                    <div class="modal-body">
                        {{comandMsg}}
                    </div>
                    <div class="modal-footer" style="height:60px">
                        <button type="button" class="btn btn-default " data-dismiss="modal" v-on:click="clickSubmit">关闭</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- 打赏模态框 -->
        <div class="modal fade bs-example-modal-sm" role="dialog" id="giveMoney">
            <div class="modal-dialog modal-sm" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        请输入打赏金额
                    </div>
                      <div class="modal-body" style="width:100%">
                          <button class="btn btn-default  moneyBtn" style="width:40%" v-for="item in moneyList" v-on:click="weChatPay(item)">{{item}}元</button>
                          <input type="number" placeholder="请输入金额" class="form-control" v-model="money">
                      </div> 
                    <div class="modal-footer" style="height:60px">
                        <button type="button" class="btn btn-primary" v-on:click="weChatPay(money)">确定</button>
                        <button type="button" class="btn btn-danger" data-dismiss="modal">关闭</button>
                    </div> 
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import global from "./global";

export default {
  name: "comand",
  data() {
    return {
      starsNum: 0,
      sendTemp: {
        htlcode: "",
        posno: "",
        opno: "",
        star: "",
        comment: " ",
        money: "",
        unique: "",
        action: "",
        typeno: "",
        openId: "",
        telno: "",
        total_fee: ""
      },
      comandText: "",
      comandMsg: "评价成功，感谢您的评价",
      money: null,
      moneyList: [1, 5, 10, 20],
      stars: [
        { num: 1, active: false },
        { num: 2, active: false },
        { num: 3, active: false },
        { num: 4, active: false },
        { num: 5, active: false }
      ],
      WxPayData: {
        appId: "",
        package: "",
        nonceStr: "",
        paySign: "",
        signType: "",
        timeStamp: ""
      }
    };
  },

  methods: {
    // 提交评价
    submit: function() {
      this.sendTemp.htlcode = global.OpnoInfo.htlcode;
      this.sendTemp.posno = global.OpnoInfo.posno;
      this.sendTemp.opno = global.OpnoInfo.opno;
      this.sendTemp.star = this.starsNum;
      this.sendTemp.comment = this.comandText;
      this.sendTemp.unique = global.userRandom;
      this.sendTemp.telno = global.telno;
      this.sendTemp.typeno = "comment";
      this.sendTemp.action = "Check";
      //先监测后评论
      if(this.starsNum==null && this.comandText==null || this.starsNum=='' && this.comandText=='' ){
        alert('你没有进行评价');
        return;
      }
      this.$http
        .post(global.PostUrl, JSON.stringify(this.sendTemp))
        .then(Response => {
          if (Response.statusText == "OK") {
            this.sendTemp.action = "Mark";
            this.$http
              .post(global.PostUrl, JSON.stringify(this.sendTemp))
              .then(Response => {
                $("#myModal").modal("show");
                if (Response.body == "Success") {
                  this.comandMsg = "评价成功，感谢您的评价";
                } else {
                  this.comandMsg = "评价失败，请联系服务员";
                }
              });
          } else {
            $("#myModal").modal("show");
            this.comandMsg = "对不起您已评价，请勿重复评价";
          }
        });
    },
    // 星星评价
    clickStar: function(starIndex) {
      this.starsNum = starIndex;
      this.stars.active = false;
      for (var i = 0; i < this.stars.length; i++) {
        this.stars[i].active = false;
      }
      for (var i = 0; i < starIndex; i++) {
        this.stars[i].active = true;
      }
    },
    // 点击打赏按钮，弹出模态框
    giveMoney: function() {
      $("#giveMoney").modal("show");
    },
    // 打赏的方法
    weChatPay: function(payMoney) {
      $("#giveMoney").modal("hide");
      this.sendTemp.action = "WxPayApi";
      this.sendTemp.htlcode = global.OpnoInfo.htlcode;
      this.money = payMoney;
      this.sendTemp.openId = global.OpenId;
      this.sendTemp.total_fee = payMoney;
      this.$http
        .post(global.PostUrl, JSON.stringify(this.sendTemp))
        .then(Response => {
          //
          this.WxPayData.appId = Response.body.appId;
          this.WxPayData.nonceStr = Response.body.nonceStr;
          this.WxPayData.package = Response.body.package;
          this.WxPayData.signType = Response.body.signType;
          this.WxPayData.timeStamp = Response.body.timeStamp;
          this.WxPayData.paySign = Response.body.paySign;
          //
          this.sendTemp.htlcode = global.OpnoInfo.htlcode;
          this.sendTemp.posno = global.OpnoInfo.posno;
          this.sendTemp.opno = global.OpnoInfo.opno;
          this.sendTemp.unique = global.userRandom;
          this.sendTemp.telno = global.telno;
          this.sendTemp.typeno = "money";
          this.sendTemp.action = "payMoney";
          this.onBridgeReady(this.sendTemp);
        });
      if (this.money == null || this.money == "" || this.money == " ") {
        alert("您输入的金额有误，请重新输入");
        return;
      }
    },
    PostPayData: function() {
      alert("InPostPayData");
      this.sendTemp.htlcode = global.OpnoInfo.htlcode;
      this.sendTemp.posno = global.OpnoInfo.posno;
      this.sendTemp.opno = global.OpnoInfo.opno;
      this.sendTemp.unique = global.userRandom;
      this.sendTemp.typeno = "Pay";
      this.sendTemp.action = "payMoney";
      this.$http.post(global.PostUrl, this.sendTemp).then(Response => {
        alert(Response.body);
        if (Response.body == "Success") {
          history.go(0);
        } else {
        }
      });
    },
    onBridgeReady: function(dataTemp) {
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: this.WxPayData.appId, //公众号名称，由商户传入
          timeStamp: this.WxPayData.timeStamp, //时间戳，自1970年以来的秒数
          nonceStr: this.WxPayData.nonceStr, //随机串
          package: this.WxPayData.package,
          signType: "MD5", //微信签名方式：
          paySign: this.WxPayData.paySign //"70EA570631E4BB79628FBCA90534C63FF7FADD89" //微信签名
        },
        function(res) {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            $.ajax({
              type: "POST",
              url: global.PostUrl,
              data: JSON.stringify(dataTemp),
              contentType: "application/json"
              // success: function(data) {

              // }
            });
          } // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
        }
      );
    }
  }
};
</script>

<style scope>
.yellowActive {
  color: gold;
}

.moneyBtn {
  margin-left: 5px;
  margin-bottom: 10px;
}

.divMagrin {
  margin-left: 5%;
  margin-right: 5%;
}
</style>



